import uniRate from '@/components/uni-rate/uni-rate.vue'
import uniGoodsNav from '@/components/uni-goods-nav/uni-goods-nav.vue'
import sku from '@/components/sku/pages/sku.vue'
// import uniPopup from "@/components/uni-popup/uni-popup.vue"
export default{
	components:{
		uniRate,
		uniGoodsNav,
		sku,
		// uniPopup
	},
	data () {
	      return {
			  popshow:false,
			  id:'',
			  data:'',
			  category:[],
			  total:'',
			  receivedCategory:false,
			  choosedSpec:'',
	        options: [{
	          icon: 'icon-kefu',
	          text: '客服'
	        }, {
	          icon: 'icon-iconfontshop-copy',
	          text: '店铺'
	        }, {
	          icon: 'icon-star',
	          text: '收藏',
	          info: 2
	        }],
	        buttonGroup: [{
	          text: '加入购物车',
	          backgroundColor: '#ff0000',
	          color: '#fff'
	        },
	        {
	          text: '立即抢购',
	          backgroundColor: '#ffa200',
	          color: '#fff'
	        }
	        ]
	      }
	    },
		onLoad(p){
			console.log(p)
			this.id=p.id
			this.getDetail()
			this.getCategory()
		},
	    methods: {
	      onClick (e) {
	        uni.showToast({
	          title: `点击${e.content.text}`,
	          icon: 'none'
	        })
	      },
	      buttonClick (e) {
	        console.log(e)
			if(e.index==0){
				uni.switchTab({
					url:'/pages/tabBar/cart',
				})
			}else{
				uni.navigateTo({
					url:'./createOrder'
				})
			}
	        // this.options[2].info++
	      },
		  chooseCategory(){
			  this.$refs.sku.specClass='show'
		  },
		  openShare(){
			  this.popshow=true
		  },
		  getDetail(){
			  var that=this
			  var url='&r=api.goods.detail&id='+this.id
			  // var params={
			  //   page:p,
			  //   pagesize: this.pageSize
			  // }
			    // var url='/wangtosale_list'
			    this.$apiPost(url).then((res) =>{
					that.data=res.data
			  	  // that.total=res.total
			  	  // that.dataList=that.dataList.concat(res.data)
			  	  // that.more=''
			    })
		  },
		  getCategory(){
			  var that=this
			  var url='&r=api.goods.detail.sku&goodsid='+this.id
			    this.$apiPost(url).then((res) =>{
			  		that.category=res.data
					that.total=res.stock
					that.receivedCategory=true
			    })
		  },
		  completeSpecChoose(e){
			  this.choosedSpec=e
		  },
		  toComments(){
			  uni.navigateTo({
			  	url:'./goodsComments?id='+this.id
			  })
		  }
	    }
}
